interface Point {
  x: number;
  y: number;
  color: string;
}

type SvgInHtml = HTMLElement & SVGElement;
type SvgCircle = HTMLElement & SVGCircleElement;

const SVG_NAMESPACE = "http://www.w3.org/2000/svg";

export class Canvas {
  points: SvgCircle[] = [];
  canvas: SvgInHtml;
  draggingObject: SvgCircle;
  dragging = false;

  constructor(canvasId: string) {
    this.canvas = <SvgInHtml>document.getElementById(canvasId);
  }

  /**
   * Adds point to canvas
   * @param x x coordinate
   * @param y y coordinate
   * @param color color of point
   */
  public addPoint(x: number, y: number, color?: string): void {
    const circle: SvgCircle = <SvgCircle>(
      document.createElementNS(SVG_NAMESPACE, "circle")
    );
    circle.setAttributeNS(null, "cx", x.toString());
    circle.setAttributeNS(null, "cy", y.toString());
    circle.setAttributeNS(null, "r", "5");
    circle.setAttributeNS(
      null,
      "style",
      `fill: ${color}; stroke: ${color}; stroke-width: 1px;`
    );
    circle.addEventListener("mousedown", this.startDrag);
    circle.addEventListener("mousemove", this.drag);
    circle.addEventListener("mouseup", this.endDrag);
    circle.addEventListener("mouseleave", this.endDrag);

    this.points.push(circle);
    this.canvas.appendChild(circle);
  }

  /**
   * Sets background color of canvas
   * @param color
   */
  public setBackgroundColor(color: string) {
    this.canvas.style.backgroundColor = color;
  }

  private startDrag(event: MouseEvent) {
    this.draggingObject = <SvgCircle>event.target;
    this.draggingObject.style.fill = "red";
    this.dragging = true;
    console.log(this.draggingObject);
  }

  private drag(event: MouseEvent) {
    if (this.dragging && this.draggingObject) {
      console.log(`Dragging on ${event.clientX} ${event.clientY}`);
      this.draggingObject.setAttributeNS(null, "cx", event.clientX.toString());
      this.draggingObject.setAttributeNS(null, "cy", event.clientY.toString());
    }
  }

  private endDrag(event: MouseEvent) {
    this.dragging = false;
    this.draggingObject.style.fill = "black";
    this.draggingObject = null;
    console.log(`Stopped dragging on ${event.clientX} ${event.clientY}`);
  }
}
